import axios from 'axios'
import qs from 'qs'

let http = axios.create({});

if (process.env.NODE_ENV == 'development') {
  http.defaults.baseURL = 'http://txjs-wechat-hnw.mion.cn/api';

} else if (process.env.NODE_ENV == 'production') {
  http.defaults.baseURL = '/api';
}

http.defaults.responseType = 'json';
// http.defaults.withCredentials = true;
http.defaults.transformRequest = [function (data) {
  return qs.stringify(data);
}];
// request,ajax
http.interceptors.request.use((config) => {
  config.headers['X-Requested-With'] = 'XMLHttpRequest';
  // 
  // config.data = qs.stringify(config.data);
  // token , token
  if (localStorage.getItem('MY_USER_INFO')) {
    config.headers.Authorization = "Bearer " + JSON.parse(localStorage.getItem('MY_USER_INFO')).data.accessToken
  }
  return config
});
// response
http.interceptors.response.use(
  (response) => {
    const data = response.data
    // code
    switch (data.status) {
      case 'success':
        return data;
        break;
      case 'fail':
        let mess='';
        for(var k in data.data){
          data.data[k].forEach((v,i)=>{
            mess+=`<p>`+v+`</p>`
          })
        }
        data.mess=mess
        return data
    }
    // code
    const err = new Error(data.description)

    err.data = data
    err.response = response

    throw err
  }, function (err) { // 200
    console.log(err)
    if (error.response === 401) {
      console.log(2)
    }
    if (err && err.response) {

      switch (err.response.status) {
        case 400:
          err.message = '';
          break;

        case 401:
          err.message = '';
          break;

        case 403:
          err.message = '';
          break;

        case 404:
          err.message = `: ${err.response.config.url}`;
          break;

        case 408:
          err.message = '';
          break;

        case 500:
          err.message = '';
          break;

        case 501:
          err.message = '';
          break;

        case 502:
          err.message = '';
          break;

        case 503:
          err.message = '';
          break;

        case 504:
          err.message = '';
          break;

        case 505:
          err.message = 'HTTP';
          break;

        default:
          err.message = `(${err.response.status})!`;
      }
    }
    return Promise.reject(err)
  }
)
axios.install = (Vue) => {
  Vue.prototype.$http = http
}

export default axios



